export default {
	data() {
		return {
			newsList: []
		}
	},
	onLoad() {
		let _this = this;
		_this.request({
			url: _this.http.getNewsList,
			data: {
				pageNum: '1',
				pageSize: '100',
				newsType: '3'
			},
			success: (res) => {
				if (res.code === '0') {
					_this.newsList = res.result;
				}
			}
		});
	},
	methods: {

	}
}
